
export default {
	onLaunch: function() {
		// 监听网络状态变化
		uni.onNetworkStatusChange(function(res) {
			console.log("resContent=", res);
			if (res.isConnected == false) {
				uni.showToast({
					title: '网络异常，请检查网络',
					icon: 'none',
					duration: 3000
				});
			}
		})
		const updateManager = uni.getUpdateManager();
		updateManager.onCheckForUpdate(function(res) {
			// 请求完新版本信息的回调
			console.log("版本", res.hasUpdate);
		});
		updateManager.onUpdateReady(function(res) {
			uni.showModal({
				title: '更新提示',
				content: '新版本已经准备好，是否重启应用？',
				success(res) {
					if (res.confirm) {
						// 新的版本已经下载好，调用 applyUpdate 应用新版本并重启
						updateManager.applyUpdate();
					}
				}
			});
		});
		updateManager.onUpdateFailed(function(res) {
			console.log("新版本下载失败");
			uni.showModal({
				title: '新版本下载失败',
				content: '请检查自己的网络是否正常',
				success: res => {},
			});
		});
	},
	onShow: function() {
		
	},
	onHide: function() {
		console.log('App Hide');
	},
	created() {
		console.log("App Create");
		uni.showLoading({
			title: '加载中',
			mask: true,
		});
		let _this = this;
		// 判断用户是否注册,已注册用户类型   
		let openid = uni.getStorageSync('openid')
		console.log("openid", openid);
		if (openid == '' || openid == null) { 
			console.log("here====");
			uni.redirectTo({
				url: '/pages/login/index',
				success() {
					uni.hideLoading()
				}
			})
		} else {
			_this.$http.changeLogin({
				openId: openid
			}).then(res => {
				uni.hideLoading()
				let type = res.data.data;
				console.log("type", type);
				if (type == -1 || type == -2) {
					uni.reLaunch({
						url: '/pages/identity/index'
					})
				} else if (type == 0) {
					uni.reLaunch({
						url: '/pages/status/index'
					})
				} else if (type == 2) {
					uni.setStorageSync('userType', 2)
					uni.reLaunch({
						url: '/pages/index/index'
					})
				} else if (type == 3) {
					uni.setStorageSync('userType', 3)
					uni.reLaunch({
						url: '/pages/index01/index'
					})
				}
			})
		}
		this.setHeight();
		this.getHeight();
		this.getWidth();
		this.statusBarHeight();
	},
	methods: {
		setHeight: function() {
			let _this = this;
			uni.getSystemInfo({
				success: function(res) {
					_this.$store.commit('setModel', res.model)
					//console.log(_this.$store.state.model)
				}
			})
		},
		statusBarHeight: function() {
			let _this = this;
			uni.getSystemInfo({
				success(res) {
					_this.$store.commit('statusBarHeight', res.statusBarHeight)
				}
			})
			//console.log(_this.$store.state.windowHeight)
		},
		getHeight: function() {
			let _this = this;
			uni.getSystemInfo({
				success(res) {
					_this.$store.commit('windowHeight', res.windowHeight)
				}
			})
			//console.log(_this.$store.state.windowHeight)
		},
		getWidth: function() {
			let _this = this;
			uni.getSystemInfo({
				success(res) {
					//console.log(res.windowWidth)
					_this.$store.commit('windowwidth', res.windowWidth)
				}
			})
			//console.log(_this.$store.state.windowWidth)
		}
	}
}
